<template>
  <div @click.self="hide" class="comp-frame_cover" v-if="show">
    <div class="comp-frame_content">
      <img :src="src" class="comp-frame_content" v-if="type === 'image'">
      <video :src="src" autoplay class="comp-frame_content" controls v-else></video>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      src: '',
      type: 'image'
    };
  },
  methods: {
    hide() {
      this.show = false;
    }
  }
};
</script>

