  display: flex;
  flex-direction: column;
}
.preview-text {
  text-align: center;
  display: flex;
  height: 20px;
  width: 120px;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}
.preview-text:hover {
  font-size: 14px;
}
