  display: inline-block;
  border: 1px dashed #d8d8d8;
  font-size: 0;
  width: 118px;
  height: 118px;
  border-radius: 4px;
}
.upload-trigger {
  display: block;
  width: 120px;
  height: 120px;
  color: #888;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
